.job-filter{
    border: 1px solid #ECECEC;
    box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.1);
    padding: 25px 15px;
}
.filter-title {
    display: block;
    font-size: 14px;
    font-weight: 600;
    color: #080036;
    padding-bottom: 25px;
}

.filter{
    padding-bottom: 30px;
}
.filter:last-child{
    padding-bottom: 0;
}

.filter-list ul li{
    padding: 20px 0;
    border-bottom: 1px solid #ECECEC;
}
.filter-list ul li:first-child{
    padding-top: 0;
}



.filter .theme-radio [type="radio"]:checked + label,
.filter .theme-radio [type="radio"]:not(:checked) + label{
    padding-left: 0;
    padding-right: 28px;
    display: block;
    color: #545454;
}

.filter .theme-radio [type="radio"]:not(:checked) + label::before{
    right: 0;
    left: auto;
}

.filter .theme-radio [type="radio"]:checked + label::before{
    right: 0;
    left: auto;
}

.filter .theme-radio [type="radio"]:checked + label::after,
.filter .theme-radio [type="radio"]:not(:checked) + label::after{
    left: auto;
    right: 4px;
}


.salary-range{
    padding-bottom: 30px;
}